import React from 'react';
import '../css/ajustesbotones.css';

function FAQ() {
    return (
      <div class="faqdiv">
        
          <h4>
           FAQ
          </h4>
          <strong>He encontrado un animal abandonado. Qu hago?</strong>
          <p>Lo primero es asistir al animal, recogerlo, evitando que pudiera ser atropellado o le pase algo malo. Llama al 112 o a Guardia civil para que puedan asistir, leer su microchip, si lo tiene.
              <br/>Si est lesionado, llvalo a un veterinario. Si tiene microchip las autoridades o el veterinario intentarn localizar al dueo. Si no lo tiene,  publcalo en grupos de facebook de tu localidad o en grupos de perros perdidos de tu CCAA, pregunta a vecinos. 
              Puedes o bien acoger al animal o solicitar que las autoridades soliciten su recogida, que segun la localidad ser una perrera o una protectora. </p>
          <strong>Quiero colaborar como voluntario</strong>
          <p>Muchas gracias por tu iniciativa de querer colaborar con los perros de nuestro refugio. Es fundamental contar con un buen equipo de voluntarios en cualquier de las tareas que haya, ya sea en el refugio, en los paseos solidarios o desde la difusin.


Para ser voluntario lo nico que te pedimos es compromiso, pues son las vidas de los perros que dependen de nuestra ayuda. Somos muy pocos lo voluntarios y con poquito que se de se logran grandes cosas para ellos. 


Si ests interesad@ en ser voluntari@ en los paseos solidarios,contacta por whatsapp al tlf. 608678544</p>
      </div>
    );
  }
  
  export default FAQ;