import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../css/ajustesbotones.css';

function QuienesSomos() {
  return (
    <div class="faqdiv">

      <h4>
        QuienesSomos
      </h4>
      <strong>QUINES SOMOS</strong>
      <p>

       AGL Animal rescue se fund en 2022 por una persona que lleva muchos aos viendo animales abandonados y decidi crear una protectora.
        Compuesta por una persona acompaada de voluntarios, que sin ellos no podramos realizar nuestra funcin.

        Actualmente no disponemos de refugio propio, trabajamos con una protectora local. </p>
      <strong>QU HACEMOS</strong>
      <p>
        Nuestra labor es el rescate y la recuperacin de animales abandonados y maltratados, con el fin de darles una segunda oportunidad.
        Carecemos de subvenciones por lo que necesitamos de vuestras aportaciones para seguir salvando VIDAS.
      </p>

    </div>
  );
}

export default QuienesSomos;